.header{margin-top:20px;padding:0 10px;border-radius:6px;box-shadow:0px 2px 20px -6px rgba(0,0,0,0.1);display:flex;justify-content:space-between;align-items:center}@media (min-width: 768px){.header{padding:20px;margin-top:40px}}@media (max-width: 360px){.header{display:flex;justify-content:center}}.header_title{text-transform:uppercase;font-size:22px}@media (min-width: 768px){.header_title{font-size:32px}}@media (max-width: 360px){.header_title{display:none}}
