.song-list{height:400px;width:100%;overflow-y:scroll;padding:0;margin:0;list-style-type:none;border-radius:6px;box-shadow:0px 2px 20px -6px rgba(0,0,0,0.1)}.song-list::-webkit-scrollbar{width:2px}.song-list::-webkit-scrollbar-track{background-color:transparent}.song-list::-webkit-scrollbar-thumb{background-color:#72848c}
