.volume{display:flex;align-items:center}@media (min-width: 768px){.volume{margin-left:auto}}.volume_icon{font-size:20px;margin-right:10px;color:#97a9b2}.volume_slider{margin:auto;-webkit-appearance:none;position:relative;overflow:hidden;height:3px;width:135px;cursor:pointer;border-radius:0}.volume_slider::-webkit-slider-runnable-track{height:33px;background:#dee7eb}.volume_slider::-webkit-slider-thumb{-webkit-appearance:none;width:0px;height:3px;box-shadow:-100vw 0 0 100vw #97a9b2}.volume_slider::-moz-range-track{height:3px;background:#dee7eb}.volume_slider::-moz-range-thumb{background:transparent;width:0px;height:3px;box-shadow:-100vw 0 0 100vw #97a9b2}.volume_slider:focus{outline:none}
