.search-bar_form{border:1px solid #ddd;border-radius:20px;margin:20px 0}.search-bar_form:focus-within{border:1px solid #10b1dc}.search-bar_input{border:none;height:100%;padding:10px;background:none}.search-bar_input:focus{outline:none}@media (min-width: 768px){.search-bar_input{padding:10px 15px}}.search-bar_button{background:none;border:none;padding:0 10px;cursor:pointer;opacity:0.7;transition:opacity 250ms}.search-bar_button:focus{outline:none}.search-bar_button:hover,.search-bar_button:focus{opacity:1}@media (min-width: 768px){.search-bar_button{padding:0 25px}}
